.about {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 154px;
    gap: 140px;
    &__container {
        width: 429.595px;
        height: 438px;
        flex-shrink: 0;
        background: linear-gradient(320deg, #2f3339 14.78%, #15181c 95.73%);
        border-radius: 29px;
        &__image {
            width: 100%;
            height: 100%;
            background-image: url("../../accset/image/about.svg");
            background-size: cover;
        }
    }
    &__content {
        &__title {
            color: #fff;

            margin-bottom: 32px;
            margin-top: 0;
            font-family: Poppins;
            font-size: 34px;
            font-style: normal;
            font-weight: 500;
            line-height: 24px; /* 70.588% */
            letter-spacing: 0.38px;
        }
        &__desc {
            max-width: 456px;
            color: #fff;

            font-family: Poppins;
            font-size: 21px;
            font-style: normal;
            font-weight: 300;
            line-height: 28px; /* 133.333% */
            letter-spacing: 0.38px;
        }
    }
}
