.header {
    display: flex;
    gap: 71px;
    justify-content: center;
  
    &__button {
        min-width: 148px;
        border-radius: 12px;
        background-color: #24272c;
        height: 60px;
        border: none;
        box-shadow: 10px 10px 12px 0px rgba(0, 0, 0, 0.25), -7px -7px 10px 0px rgba(174, 168, 168, 0.25);


        font-family: var(--font-family);
        span {
            color: #fff;
            font-size: 22px;
            font-style: normal;
            font-weight: 500;
            line-height: 24px;
            letter-spacing: 0.38px;
        }
    }
}
